.card {
  padding: 0;
  position: relative;
  transition: .3s all ease-in-out;
  height: 225px;
  box-sizing: border-box;

  &:hover, &:focus {
    transform: scale(1.13);
    padding: 16px;
    box-shadow: 0px 6px 5px rgba(0, 0, 0, 0.24), 0px 9px 18px rgba(0, 0, 0, 0.18);
    background: #FFFFFF;

    .card__button-like {
      visibility: visible;
    }
  }

  &__image {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__button-like {
    visibility: hidden;
    width: 36px;
    height: 32px;
    position: absolute;
    right: 20px;
    bottom: 24px;
    padding: 0;
    border: none;
    cursor: pointer;
    background-color: transparent;
    background-size: 100%;
    box-sizing: border-box;
    background-image: url("../../images/like-icon.svg");
    transition: .3s all ease-in-out;

    &:hover, &:focus {
      background-image: url("../../images/like-hover.svg");
    }

    &_active {
    background-image: url("../../images/like-icon-active.svg");

    }
  }
}